import { Grid, Typography, useTheme } from '@mui/material';
import PostCard from 'components/cards/PostCard';
import Layout from 'components/Layout';
import Paginator from 'components/Paginator';
import RowList from 'components/RowList';
import { Link, type PageProps } from 'gatsby';
import { useLanguage } from 'hooks/useLanguage';
import React, { useMemo } from 'react';

const PostsPage = ({ data }: PageProps<Queries.PostsPageQuery>) => {
  const theme = useTheme();

  const {
    allMarkdownRemark: { edges, pageInfo },
  } = data;
  const { language: lang } = useLanguage();

  const posts = useMemo(() => edges.map(p => p.node), [edges]);

  return (
    <Layout title={<Layout.Title title="Posts" description="" />}>
      <RowList>
        {posts.map(p => {
          const path = p.fields?.path ?? '/';
          const title = p.frontmatter?.title ?? '';
          const subTitle = p.frontmatter?.subTitle ?? '';
          const thumbnail = p.frontmatter?.thumbnail?.childImageSharp?.gatsbyImageData;
          const date = p.frontmatter?.date ?? '1991-01-01';
          const categories = p.frontmatter?.categories?.filter(Boolean) ?? [];

          return (
            <Grid
              item
              xs={12}
              key={p.frontmatter?.title}
              sx={{
                padding: `${theme.spacing(0.25)} 0`,
                '& a': {
                  textDecoration: 'none',
                  display: 'block',
                  borderRadius: theme.shape.borderRadius * 2,
                  transition: 'background-color 0.2s ease-in-out',
                },
                '& a:hover': {
                  backgroundColor:
                    theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.01)' : 'rgba(255, 255, 255, 0.025)',
                },
                '& + &': {
                  borderTop: `1px solid ${theme.palette.divider}`,
                },
              }}
            >
              <Link to={path}>
                <PostCard
                  title={title}
                  subTitle={subTitle}
                  thumbnail={thumbnail}
                  date={date}
                  badge={
                    categories.length > 0 ? (
                      <Typography variant="caption" color="text.secondary">
                        {categories.join(' / ')}
                      </Typography>
                    ) : null
                  }
                />
              </Link>
            </Grid>
          );
        })}
      </RowList>
      <Paginator pageCount={pageInfo.pageCount} currentPage={pageInfo.currentPage} defaultUrl="/posts/" lang={lang} />
    </Layout>
  );
};

export default PostsPage;
